//import common from '../../common/common.js'
import helloComp from '../../components/helloComp.vue';
import trailerStars from '../../components/trailerStars.vue';
export default {
	data() {
		return {
			carouselList: [],
			hotList: [],
			trailerList: [],
			animationData:{},
			guessULikeList:[],
			animationDataArr:[
				{},{},{},{},{}
			]
		};
	},
	components: {
		'hello-Comp': helloComp,
		trailerStars
	},
	onUnload(){
		this.animationData = {};
		animationDataArr:[
			{},{},{},{},{}
		]
	},
	onPullDownRefresh(){
		this.refresh()
	},
	onLoad() {
		var me = this;
		

		this.animation = uni.createAnimation();

		
		// uni.request({
		// 	url: me.serverUrl + '/index/guessULike?qq=1931889491', //仅为示例，并非真实接口地址。
		// 	method: 'POST',
		// 	success: res => {
		// 		console.log(res.data);
		// 		if (res.data.status == 200) {
		// 			me.guessULikeList = res.data.data;
		// 		}
		// 	}
		// });
		//请求轮播图数据
		uni.request({
			url: me.serverUrl + '/index/carousel/list?qq=1931889491', //仅为示例，并非真实接口地址。
			method: 'POST',
			success: res => {
				console.log(res.data);
				if (res.data.status == 200) {
					me.carouselList = res.data.data;
				}
			}
		});
		uni.request({
			url: me.serverUrl + '/index/movie/hot?qq=1931889491&type=superhero', //仅为示例，并非真实接口地址。
			data: {
				type: 'superhero'
			},
			method: 'POST',
			success: res => {
				console.log(res.data);
				if (res.data.status == 200) {
					me.hotList = res.data.data;
				}
			}
		});
		uni.request({
			url: me.serverUrl + '/index/movie/hot?qq=1931889491&type=trailer', //仅为示例，并非真实接口地址。
			method: 'POST',
			success: res => {
				console.log(res.data);
				if (res.data.status == 200) {
					me.trailerList = res.data.data;
				}
			}
		});
		me.refresh();
	},
	methods: {
		
		refresh(){
			var me = this;
			
			uni.showLoading({
				mask:true
			})
			//uni.showNavigationBarLoading()
			
			
			uni.request({
				url: me.serverUrl + '/index/guessULike?qq=1931889491', //仅为示例，并非真实接口地址。
				method: 'POST',
				success: res => {
					console.log(res.data);
					if (res.data.status == 200) {
						me.guessULikeList = res.data.data;
					}
				},
				complete:()=>{
					uni.stopPullDownRefresh();
					uni.hideLoading()
					uni.stopPullDownRefresh()
				}
			});
		},
		praiseMe(e){

			
			
			var index = e.currentTarget.dataset.index;
			console.log()
			this.animation.translateY(-60).opacity(1).step({
				duration:400
			});
			// this.animationData = this.animation.export();
			this.animationData = this.animation;
			this.animationDataArr[index] = this.animationData.export;
			setTimeout(function() {
				this.animation.translateY(0).opacity(0).step({
					duration:0
				});
				this.animationData = this.animation;
				this.animationDataArr[index] = this.animationData.export;
			}.bind(this), 500);

		}
	}
};
